.curiosities {
  padding: 40px 0 60px;
  color: #fff;
  background: #1d2a4c;

  @include min($xs) {
    background: linear-gradient(to right, rgba(29, 42, 76, 1) 50%, rgba(29, 42, 76, .5)), url("img/bg-curiosities.jpg") right no-repeat;
  }

  @include min($sm) {
    padding: 75px 0 125px;
  }

  @include min($lg) {
    padding: 95px 0 165px;
    background: linear-gradient(to right, rgba(29, 42, 76, 1) 65%, rgba(29, 42, 76, .5)), url("img/bg-curiosities.jpg") right no-repeat;
  }
  @include btw($md, $lap) {
    background-position: left center, calc(100% + 150px)center;
  }
  @include btw($lap, $xl) {
    background-position: left center, calc(100% + 50px)center;
  }

  &__title {
    margin-bottom: 37px;

    @include max($xs) {
      text-align: center;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;

    @include max($xs) {
      align-items: center;
      text-align: center;
    }

    @include max($md) {
      flex-direction: column;
    }

    @include min($md) {
      width: 670px;
    }

    @include min($lg) {
      width: 832px;
    }
  }

  &__col {
    max-width: 100%;

    @include min($xs) {
      width: 312px;
    }

    @include min($lg) {
      width: 387px;
    }

    &:first-child {

      @include max($md) {
        margin-bottom: 40px;
      }
    }
  }

  img {
    max-width: 100%;

    @include min($xs) {
      width: 312px;
      height: 212px;
    }

    @include min($lg) {
      width: 387px;
      height: 264px;
    }
  }

  &__imgCaption {
    font-size: 1.4rem;
    line-height: (24/18);
    color: #c1cae2;
    font-weight: 100;
    margin-top: 10px;

    @include min($sm) {
      font-size: 1.6rem;
      margin-top: 20px;
    }

    @include min($lg) {
      font-size: 1.8rem;
    }
  }
}
