.cookies {
	display: flex;
	align-items: center;
	background: #00b0f0;
	color: #fff;
	letter-spacing: .025em;
	position: fixed;
	bottom: -1px;
	left: 0;
	width: 100%;
	z-index: 50;
	font-size: 1.2rem;
	padding: 10px;
	transform: translateY(100%);
	transition: transform .3s ease-in-out;

	&--active {
		transform: none;
	}

	.container {
		display: flex;
		align-items: center;
	}

	&__wrap {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__close {
		transition: color 0.1s ease-in-out;
		font-weight: 600;
		cursor: pointer;
		font-size: 1.1rem;
		background-color: #1A213B;
		padding: 10px 15px;
		text-transform: uppercase;
		margin-left: 20px;

		@include min($sm) {
			font-size: 1.2rem;
		}

		span {

			&:first-child {

				@include max($sm) {
					display: none;
				}
			}

			&:last-child {

				@include min($sm) {
					display: none;
				}
			}
		}
	}

	&__text {
		display: flex;
		font-size: 1.2rem;

		@include min($sm) {
			font-size: 1.4rem;
		}

		@include min($sm) {
			width: 650px;
		}
	}
}
