.joinUs {
  position: relative;
  background: url("img/bg.jpg") center/cover no-repeat;
  height: 100vh;
  color: #fff;
  text-align: center;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  &__contentWrap {
    z-index: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;

    @include min($md) {
      margin-top: 100px;
    }
  }

  &__title {
    letter-spacing: -.025em;
    margin-bottom: 24px;
    font-weight: 700;
  }

  &__btn {
    margin-top: 40px;
  }

  &__scrollDown {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 40px;
    z-index: 1;

    @include max($lg) {
      display: none;
    }

    p {
      font-size: 1.1rem;
      opacity: .7;
    }
  }

  &__circle {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 2px solid #fff;
    margin: 0 auto 10px;
    position: relative;
    overflow: hidden;

    span {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -38%) rotate(-90deg);
      animation: 1.5s scroll infinite;

      &::after,
      &::before {
        content: "";
        display: block;
        width: 8px;
        height: 3px;
        background: #fff;
      }

      &::after {
        transform: rotate(45deg);
      }

      &::before {
        transform: rotate(-45deg);
      }
    }
  }
}

@keyframes scroll {

  0% {
    top: -8px;
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  100% {
    top: 30px;
    opacity: 0;
  }
}
