.advantages {
  padding: 40px 0 50px;

  @include min($sm) {
    padding: 87px 0 105px;
  }

  &__titleWrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 63px;
    text-align: center;
    width: 65%;

    h1 {
      font-weight: 700;
      color: #1d2a4c;
      letter-spacing: -.025em;
    }

    p {
      color: #6d6d6d;
      line-height: (34/18);
      font-size: 1.4rem;

      @include min($sm) {
        font-size: 1.6rem;
      }

      @include min($lg) {
        font-size: 1.8rem;
      }
    }
  }

  &__line {
    margin: 25px 0 34px;

    @include min($sm) {
      margin: 32px 0 53px;
    }
  }

  &__columnWrap {
    display: flex;

    @include max($md) {
      flex-direction: column;
    }
  }

  &__column {
    width: 100%;

    @include min($md) {
      width: 50%;
    }

    &:last-child > .advantages__box {

      @include max($md) {
        margin-top: 62px;
      }
    }

    p {
      margin-bottom: 40px;
      font-size: 1.4rem;
      line-height: (28/14);
      color: #6d6d6d;

      &:last-child {
        margin-bottom: 0;
      }

      & > span {
        font-weight: 700;
        color: #1d2a4c;
      }
    }
  }

  &__box {
    display: flex;
    margin-top: 62px;

    @include max($xs) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    @include min($xs) {
      padding-right: 60px;
    }

    &:first-child {
      margin-top: 0;
    }

    & > span {
      font-size: 10.1rem;
      color: #f4f4f5;
      font-weight: 700;
      margin: -8px 12px 0;
    }
  }
}
