.btn {
  background-color: #00b0f0;
  padding: 10px 25px;
  border-radius: 25px;
  display: inline-block;
  color: #fff;
  font-size: 1.4rem;
  overflow: hidden;
  position: relative;

  @include min($lg) {
    padding: 15px 35px;
    border-radius: 35px;
  }

  span {
    position: relative;
    z-index: 3;
    cursor: pointer;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: green;
    width: 0;
    height: 0;
    transition: all .3s ease-in-out;
    border-radius: 50%;
    z-index: 2;
  }

  &:hover {

    p {
      cursor: pointer;
    }

    &::after {
      width: 200px;
      height: 200px;
      cursor: pointer;
    }
  }
}
