.slider {
  position: relative;

  .swiper-container {
    height: 64.5vw;

    @include min($lg) {
      height: 815px;
    }
  }

  &__textWrap {
    position: absolute;
    left: 50%;
    bottom: 30px;
    transform: translateX(-50%);
    color: #fff;
    z-index: 2;
    text-align: center;

    @include min($xs) {
      bottom: 40px;
    }

    @include min($sm) {
      bottom: 60px;
    }

    p {

      &:first-child {
        font-size: 1.2rem;
        margin-bottom: 7px;
        font-weight: 500;

        @include min($sm) {
          font-size: 1.6rem;
        }

        @include min($md) {
          margin-bottom: 14px;
        }

        @include min($lg) {
          font-size: 2.1rem;
        }

      }

      &:last-child {
        font-size: 1rem;
        opacity: .6;

        @include min($sm) {
          font-size: 1.3rem;
        }

        @include min($lg) {
          font-size: 1.5rem;
        }
      }
    }
  }

  .swiper-slide {
    background-position: center;
    background-size: cover;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      background: linear-gradient(to bottom, transparent, #1d2a4c);
      width: 100%;
      height: 40%;
      z-index: 2;
    }
  }

  .swiper-pagination-bullets {
    bottom: 5px;

    @include min($xs) {
      bottom: 15px;
    }

    @include min($sm) {
      bottom: 30px;
    }
  }

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    opacity: .6;
    background: transparent;
    border: 2px solid #fff;
  }

  .swiper-pagination-bullet-active {
    background: #00b0f0;
    border: 2px solid #00b0f0;
  }

  .swiper-button-prev,
  .swiper-button-next {
    width: 50px;
    height: 50px;
    background: rgba(255, 255, 255, .5);
    border-radius: 50%;
    transition: .3s;


    @include min($sm) {
      width: 95px;
      height: 95px;
    }

    .icon-arrow2 {
      position: absolute;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;

      @include max($sm) {
        transform: translate(-50%, -50%) scale(.6);
      }
    }

    &:hover {
      background: rgba(255, 255, 255, .7);
    }
  }

  .swiper-button-prev {
    transform: rotate(180deg);
    left: 10px;

    @include min($xs) {
      left: 30px;
    }

    @include min($sm) {
      left: 60px;
    }

    @include min($md) {
      left: 90px
    }
  }

  .swiper-button-next {
    right: 10px;

    @include min($xs) {
      right: 30px;
    }

    @include min($sm) {
      right: 60px;
    }

    @include min($md) {
      right: 90px
    }
  }
}
