.formSection {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 19;
  display: none;

  &::after {
    content: "";
    position: fixed;
    display: block;
    left: -20%;
    top: -20%;
    width: 140%;
    height: 140%;
    background: rgba(0, 0, 0, .5);
  }

  &__body {
    position: fixed;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    background: #fff;
    z-index: 19;
    font-family: "Usual Medium", sans-serif;
    width: 90vw;
    padding: 20px;

    @include max($sm) {
      overflow-y: scroll;
      overflow-x: hidden;
      bottom: 20px;
    }

    @include min($sm) {
      padding: 40px 60px 80px;
      width: 470px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -45%);
    }

    @include maxh($sm) {
      overflow-y: scroll;
      overflow-x: hidden;
      bottom: 20px;
      top: 100px;
      transform: translate(-50%, 0);
    }

  }

  &__checkbox {
    appearance: none;
    width: 25px;
    height: 25px;
    display: inline-block;
    outline: 0;
    border: 3px solid #6e6e6e;
    vertical-align: middle;
  }

  &__btn {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    position: absolute;
    top: 6px;
    right: 6px;
    background: #00b0f0;
    z-index: 40;
    transition: .2s ease-in-out;
    z-index: 26;

    @include min($xs) {
      top: 18px;
      right: 18px;
    }

    @include min($lg) {
      top: -18px;
      right: -18px;
    }

    &::after,
    &::before {
      content: "";
      display: block;
      width: 1px;
      height: 16px;
      background: #fff;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(35deg);
    }

    &::before {
      transform: translate(-50%, -50%) rotate(-35deg);
    }

    &:hover {
      background: #1d2a4c;
      cursor: pointer;
    }
  }

  &__title,
  &__caption {
    text-align: center;
  }

  &__title {
    color: #1d2a4c;
    font-weight: 700;
    margin-bottom: 40px;
  }

  &__caption {
    font-size: 1.5rem;
    line-height: 1.4;
    color: #6d6d6d;
    margin-bottom: 30px;
  }

  &__form {

    
    span {
      color: red;
    }
  }

  &__label {
    font-size: 1.3rem;
    color: #1d2a4c;
    font-weight: 600;
    margin-bottom: 2px;

    &--blackText {
      color: #000;
    }
  }

  &__input,
  &__textarea {
    border: 2px solid #dfdfdf;
    border-radius: 7px;
    font-size: 1.5rem;
    padding: 15px 20px;
    width: 100%;
    margin-bottom: 15px;
    outline: 0;
  }

  &__textarea {
    resize: none;
    height: 148px;
  }

  &__requiredFields {
    font-size: 1.3rem;
    color: #838383;
    font-weight: 600;
    margin: 20px 0;
  }

  &__btnWrap {
    text-align: center;
  }

  &__btnBottom {

    span {
      color: #fff;
    }
  }
  label.error {
    color: #d20511;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1rem;
    @include min($sm) {
      display: inline-block;
      margin-left: .5rem;
    }

  }
  .message__success {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #FFF;
    z-index: 2;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility 0s linear .5s;
    &.is-active {
      opacity: 1;
      visibility: visible;
      transition: opacity .3s ease-in-out, visibility 0s linear;

    }
    p {
      font-size: 2rem;
      text-transform: uppercase;
      &:nth-child(1) {
        font-size: 3rem;
        margin-bottom: 2rem;
        color: #00b0f0;
      }
    }
  }
  span.notRequired {
    color: #868686;
    font-weight: 400;
    font-size: 1.3rem;
    margin-left: .5rem;
  }
  .wrap--dropzone {
    text-align: left;
    margin-top: 1rem;
    .btn {
      padding: .8rem 3rem;
      
    }
  }
  .label--dropzone {
    margin-top: 2rem;
    display: block;
  }
  .dz-details {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
  }
  .dz-size {
    font-size: 1.5rem;
    span {
      color: #1d2a4c;
    }

  }
  .dz-filename {
    font-size: 1.5rem;
    span {
      color: #1d2a4c; 
    }
  }
  .dz-success-mark, .dz-error-mark {
    display: none;
  }
  .dz-remove {
    font-size: 1.2rem;
    text-transform: uppercase;
    color: #1d2a4c;
    text-decoration: underline;
  }
  .dz-error-message {
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  #input-dropzone {
    display: none;
  }
}

.displayForm-js {
  display: block;
}
