*,
*:before,
*:after {
	box-sizing: inherit;
}

html, body {
	height: 100%;
}

html {
	font-size: 62.5%;
	box-sizing: border-box;
}

body {
	font-size: 1.6rem;
	font-family: 'Usual', sans-serif;
	position: relative;
	width: 100%;
	height: 100%;
}

a {
	text-decoration: none;
	color: inherit;
}

h1 {
	font-size: 2rem;

	@include min($sm) {
		font-size: 3.5rem;
	}

	@include min($lg) {
		font-size: 6.2rem;
	}
}

h5 {
	font-size: 1.8rem;

	@include min($sm) {
		font-size: 2rem;
	}

	@include min($lg) {
		font-size: 2.8rem;
	}
}
