.recruiter {
  display: flex;
  align-items: center;
  width: 100%;

  @include max($md) {
    flex-direction: column;
  }

  &__img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-size: cover;
    background-position: top;
    margin-bottom: 25px;

    @include min($md) {
      margin: 0 16px 0 0;
    }
  }

  &__dataWrap {
    display: flex;
    flex-direction: column;

    @include max($md) {
      text-align: center;
    }

    &--primary {
      color: #1d2a4c;

      span, a {
        color: #6d6d6d;
      }
    }

    &--whiteText {
      color: #fff;
    }

    p {
      font-size: 2.1rem;
      margin-bottom: 16px;
    }

    span, a {
      font-size: 1.4rem;
      line-height: (23/14);
    }

    a:hover {
      text-decoration: underline;
    }
  }
}
