.about {
  padding: 45px 0;
  text-align: center;

  @include min($sm) {
    padding: 83px 0 95px;
  }

  &__title,
  &__text {

    @include min($md) {
      margin: 0 auto;
      width: 80%;
    }
  }

  &__title {
    line-height: (42/28);
    color: #1d2a4c;
    margin-bottom: 21px;
    font-weight: 600;
  }

  &__text {
    line-height: (34/18);
    color: #6d6d6d;
    font-size: 1.4rem;

    @include min($sm) {
      font-size: 1.6rem;
    }

    @include min($lg) {
      font-size: 1.8rem;
    }
  }
}
