.header {
  position: fixed;
  width: 100%;
  height: 100px;
  color: #fff;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, .3);
  z-index: 20;
  transform: translate3d(0, 0, 0);
  transition: .2s ease-in-out;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {

    @include max($xxs) {
      width: 160px;
    }
  }

  &__navList {
    transition: .4s ease-in-out;

    @include min($md) {
      display: flex;
    }

    @include max($md) {
      text-align: center;
      position: absolute;
      top: -200px;
      left: 0;
      width: 100%;
    }
  }

  &__navItem {

    @include max($md) {
      background: #1d2a4c;
    }
  }

  &__navItemLink {
    padding: 16px;
    border-radius: 32px;
    overflow: hidden;
    font-size: 1.4rem;
    display: block;
    position: relative;

    @include max($md) {
      display: block;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: #1d2a4c;
      z-index: -1;
      width: 0;
      height: 0;
      transition: all .3s ease-in-out;
      border-radius: 50%;
    }

    &:hover {

      &::after {
        width: 180px;
        height: 180px;
      }
    }
  }

  &__burger {
    width: 40px;
    height: 28px;
    position: relative;
    margin-bottom: 18px;

    @include min($xxs) {
      margin-bottom: 12px;
    }

    @include min($md) {
      display: none;
    }

    span {
      position: absolute;
      left: 0;
      width: 40px;
      height: 4px;
      background: #fff;
      transition: .3s ease-in-out;
      border-radius: 2px;

      &:first-child {
        top: 0;
      }

      &:nth-child(2) {
        top: 12px;
      }

      &:last-child {
        top: 24px;
      }
    }
  }
}

.burger-active {

  span {

    &:not(:nth-child(2)) {
      top: 12px;
    }

    &:first-child {
      transform: rotate(135deg);
    }

    &:last-child {
      transform: rotate(-135deg);
    }

    &:nth-child(2) {
      transform: translateX(100%);
      opacity: 0;
    }
  }
}

.menu-active {

  .header__navList {

    @include max($md) {
      top: 0;
    }
  }
}

.narrow-header {
  height: 70px;
  background: #1d2a4c;
  border: none;

  .header__navItemLink {

    &::after {
      background: #00b0f0;
    }
  }
}
