.contact {
  background-color: #1d2a4c;
  color: #fff;
  padding: 63px 0 50px;

  @include min($sm) {
    padding: 126px 0 100px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include max($sm) {
      flex-direction: column-reverse;;
    }
  }

  &__col {

    &:first-child {

      @include max($sm) {
        text-align: center;
        margin-top: 40px;
      }

      @include min($sm) {
        width: 50%;
      }

      p {
        color: #c1cae2;
        line-height: (34/18);
        font-weight: 100;
        margin: 20px 0 25px;
        font-size: 1.4rem;

        @include max($sm) {
          padding: 0 10%;
        }

        @include min($sm) {
          font-size: 1.6rem;
        }

        @include min($lg) {
          font-size: 1.8rem;
        }
      }
    }
  }
}
