.footer {
  padding: 40px 0 50px;

  @include min($sm) {
    padding: 80px 0 95px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid #dfdfdf;
    padding-bottom: 60px;

    @include min($md) {
      justify-content: space-between;
    }
  }

  &__col {

    &:not(:last-child) {

      @include max($sm) {
        margin-bottom: 60px;
      }
    }

    &:last-child {

      @include btw($sm, $md) {
        margin-top: 60px;
      }
    }

    &:not(:nth-child(2)) {
      display: flex;
      justify-content: center;
    }

    &:first-child {
      width: 100%;

      @include min($sm) {
        width: 50%;
      }

      @include min($md) {
        width: 26%;
      }

      @include min($lg) {
        width: 25%
      }
    }

    &:nth-child(2) {
      display: flex;
      justify-content: space-between;

      @include max($sm) {
        width: 100%;
      }

      @include max($xxs) {
        flex-direction: column;
      }

      @include min($xxs) {
        justify-content: center
      }

      @include min($sm) {
        width: 50%;
      }

      @include btw($md, $lg) {
        width: 22%;
        flex-direction: column;
      }

      @include min($lg) {
        width: 28%;
      }

      a, span {
        font-size: 1.5rem;
        line-height: (42/15);
        color: #1d2a4c;
        display: block;
      }

      a:hover {
        text-decoration: underline;
      }

      li {
        font-size: 1.3rem;
        line-height: (26/13);
        color: #6d6d6d;
        position: relative;
        padding-left: 20px;

        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #1d2a4c;
        }
      }
    }

    &:last-child {
      width: 100%;

      @include min($md) {
        width: auto;
      }
    }
  }

  &__subCol {

    &:first-child {

      @include min($xxs) {
        margin-right: 30px;
      }
    }
  }

  &__circle {
    width: 235px;
    height: 235px;
    border-radius: 50%;
    background: #e9eaeb;
    position: relative;

    &:hover {

      .footer__btn {

        &::after {
          width: 50px;
          height: 50px;
        }

        span {
          animation: arrow .5s ease-in-out;
        }
      }
    }
  }

  &__textWrap {
    position: absolute;
    left: 45px;
    top: 25px;

    p {

      &:first-child {
        color: #6d6d6d;
        font-size: 1.5rem;
        line-height: 2;
      }

      &:last-child {
        font-size: 1.3rem;
      }
    }
  }

  &__btn {
    position: absolute;
    top: 35px;
    right: 0;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background: #1d2a4c;
    overflow: hidden;

    span {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: .5s;
      z-index: 3;

      &::after,
      &::before {
        content: "";
        display: block;
        width: 8px;
        height: 3px;
        background: #fff;
      }

      &::after {
        transform: rotate(-45deg);
      }

      &::before {
        transform: rotate(45deg);
      }
    }

    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 0;
      height: 0;
      background: #00b0f0;
      z-index: 2;
      border-radius: 50%;
      transition: all .2s ease-in-out;
    }
  }

  &__catalogImg {
    position: absolute;
    bottom: -35px;
    right: -25px;
  }

  &__cloud {
    position: relative;
    background: #1d2a4c;
    color: #fff;
    padding: 30px 200px 30px 30px;
    white-space: nowrap;

    @include max($xs) {
      width: 100%;
    }

    @include min($sm) {
      padding: 40px 200px 40px 40px;
    }

    a:hover {
      text-decoration: underline;
    }

    p {

      span {
        color: #80889b;
      }

      &:first-child {
        margin-bottom: 25px;
        font-size: 1.6rem;

        @include min($sm) {
          font-size: 2rem;
        }

        @include min($lg) {
          font-size: 2.4rem;
        }
      }
    }

    p:not(:first-child), a {
      font-size: 1.2rem;
      line-height: (38/15);

      @include min($sm) {
        font-size: 1.3rem;
      }

      @include min($lg) {
        font-size: 1.5rem;
      }
    }

    .icon-cloudHook {
      position: absolute;
      bottom: -27px;
      left: 30px;

      @include min($sm) {
        left: 40px;
      }
    }
  }

  &__questionMark {
    position: absolute;
    right: 0;
    bottom: 0;
    overflow: hidden;
    width: 87px;
    height: 87px;

    &::after {
      content: "";
      width: 87px;
      height: 87px;
      border: 4px solid #49536d;
      border-radius: 50%;
      position: absolute;
      bottom: -10px;
      right: -10px;
    }

    &::before {
      content: "?";
      position: absolute;
      bottom: 10px;
      right: 23px;
      font-size: 50px;
      color: #49536d;
    }
  }

  &__rowCopy {
    font-size: 1.3rem;
    color: #6d6d6d;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;

    @include max($md) {
      flex-direction: column;
    }
  }

  &__copy,
  &__realization {
    display: flex;
    align-items: center;
  }

  &__copy {

    @include max($md) {
      margin-bottom: 20px;
    }

    @include max($sm) {
      flex-direction: column;
      text-align: center;
    }

    p {
      margin-left: 15px;

      @include max($sm) {
        margin-top: 10px;
      }
    }
  }

  &__realization {

    p {
      margin-right: 5px;
    }
  }
}

@keyframes arrow {

  0% {
    left: 50%;
  }

  50% {
    left: 150%;
  }

  51% {
    left: -200%;
  }

  100% {
    left: 50%;
  }
}
