.recruitment {
  padding: 50px 0;

  @include min($sm) {
    padding: 105px 0 115px;
  }

  &__titleWrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 37px;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    color: #1d2a4c;
    letter-spacing: -.025em;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;

    &--alignCenter {
      align-items: center;
      margin-bottom: 60px;
    }

    &--alignTop {
      align-items: flex-start;

      h6, p {
        color: #1d2a4c;
        font-weight: 600;
      }

      h6 {
        margin-bottom: 18px;
        font-size: 1.8rem;

        @include min($sm) {
          font-size: 2rem;
        }

        @include min($lg) {
          font-size: 2.3rem;
        }
      }

      li, p {
        line-height: (34/16);

        @include max($sm) {
          font-size: 1.4rem;
        }
      }

      li {
        color: #6d6d6d;
      }
    }
  }

  &__colAbout {

    @include max($md) {
      margin-bottom: 40px;
    }

    @include min($md) {
      width: 50%;
    }

    @include min($lg) {
      padding-right: 5%;
      width: 70%;
    }

    p {
      color: #6d6d6d;
      font-size: 1.4rem;
      line-height: (34/18);

      @include max($md) {
        text-align: center;
      }

      @include min($sm) {
        font-size: 1.6rem;
      }

      @include min($lg) {
        font-size: 1.8rem;
      }
    }
  }

  &__recruiter {
    @include btw($md, $lg) {
      justify-content: center;
      width: 50%;
    }

    @include min($lg) {
      width: 30%;
    }
  }

  &__colRequirements {
    width: 100%;

    @include min($sm) {
      width: 50%;
    }

    @include min($md) {
      width: 25%;
    }

    @include min($lg) {
      width: 35%;
    }

    li {
      padding-left: 30px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        width: 6px;
        height: 6px;
        display: block;
        background-color: #1d2a4c;
      }

      &:last-child {
        margin-bottom: 8px;
      }
    }
  }

  &__colHowStart {
    width: 100%;

    @include max($sm) {
      margin-top: 40px;
    }

    @include min($sm) {
      width: 50%;
    }

    @include min($md) {
      width: 25%;
    }

    @include min($lg) {
      width: 35%;
    }

    li {
      position: relative;
      padding-bottom: 16px;

      &::after {
        content: "";
        position: absolute;
        background-image: url("img/arrow.png");
        width: 16px;
        height: 18px;
        left: -1px;
        bottom: 0px;
      }
    }
  }

  &__colWhyUs {
    width: 100%;

    @include max($sm) {
      margin-top: 40px;
    }

    @include btw($sm, $md) {
      margin-top: 60px;
      width: 50%;
    }

    @include min($md) {
      width: 50%;
    }

    @include min($lg) {
      width: 30%;
    }

    h6 {

      @include btw($md, $lg) {
        text-align: center;
      }
    }

    p {

      @include btw($md, $lg) {
        margin-left: 100px;
      }
    }

    li {
      position: relative;
      padding-left: 25px;

      @include btw($md, $lg) {
        margin-left: 100px;
      }

      .icon-hook {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  &__btnWrap {
    margin-top: 40px;
    text-align: center;

    @include btw($xs, $md) {
      text-align: left;
    }
  }
}
